@import '../../common/style/sass/variable';

.navigation {
  position: relative;
  display: flex;
  justify-content: space-around;
  background-color: $mainbackgroundColor;
  align-items: center;
  min-width: 400px;
  height: 80px;
  text-align: end;
  font-family: 'Syne Mono', monospace;
  color: $secondTextColor;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: .02em;
  line-height: .881em;
  margin: 50px 40px;

  a {
    transition: color .2s ease-out;
  }
}
