@import '../../common/style/sass/variable';

.contain {
  background-color: $mainbackgroundColor;
  border: 1px solid;

  .head {
    display: flex;
    justify-content: center;
    background-color: $mainbackgroundColor;
    height: 100px;
  }

  .form {
    display: flex;
    flex-direction: column;
    height: 500px;
    border: 1px solid;
    align-items: center;
    background-color: $mainbackgroundColor;
    justify-content: center;

    .input {
      width: 250px;
      height: 30px;
      border-radius: 10px;
      margin: 5px;
      @media screen and (max-width: 406px) {
        max-width: 150px;
      }
    }

    .textarea {
      width: 250px;
      height: 130px;
      border-radius: 10px;
      @media screen and (max-width: 406px) {
        max-width: 150px;
      }
    }
  }
}
